html {
  box-sizing: border-box;
  background: linear-gradient(45deg, #42a5f5 0%, #478ed1 50%, #0d47a1 100%);
  font-size: 10px;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  text-align: center;
  margin: 0;
  font-family: Inconsolata, monospace;
}

.display__time-left {
  color: #fff;
  text-shadow: 4px 4px rgba(0, 0, 0, .05);
  margin: 0;
  font-size: 20rem;
  font-weight: 100;
}

.timer {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

.timer__controls {
  display: flex;
}

.timer__controls > * {
  flex: 1;
}

.timer__controls form {
  flex: 1;
  display: flex;
}

.timer__controls input {
  border: 0;
  flex: 1;
  padding: 2rem;
}

.timer__button {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background: rgba(0, 0, 0, .1);
  border-top: 0;
  border-bottom: 3px solid rgba(0, 0, 0, .2);
  border-left: 0;
  border-right: 1px solid rgba(0, 0, 0, .2);
  padding: 1rem;
  font-family: Inconsolata, monospace;
  font-size: 2rem;
}

.timer__button:hover, .timer__button:focus {
  background: rgba(0, 0, 0, .2);
  outline: 0;
}

.display {
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.display__end-time {
  color: #fff;
  font-size: 4rem;
}

